<template>
    <div class="popup_wrap" :style="{'display':fnIsOpen()}">
      <!-- cctv 페이지 팝업내용 이기 때문에 popup_box, cctv_popup_box 동시사용 -->
      <div :class="popupClass">

          <div class="popup_hd">
              <h3 class="pop_title">{{title}}</h3>
              <a href="javascript:void(0)" @click="popupClose()" class="btn_pop_close"><img src="/static/EXcode/static/images/ic_close.png" alt=""></a>
          </div>
         
          <!-- 팝업 컨텐츠 -->
          <slot></slot>
      
      </div>

    </div>
</template>

<script>
  export default {
    name: 'PopupLayout',
    props: {
      title: '',
      popupClass: {
        type: Array,
        default: () => {
          return ['popup_box']
        }
      },
      isOpen: Boolean
    },
    data(){
      return {
        data: {}
      }
    },
    methods: {
      fnIsOpen() {
        return this.isOpen ? '': 'None'
      },
      popupClose() {
        this.$emit("onClose", this.data)
      }
    }

  }
</script>


