<template>

    <DefaultTable
      :tblClass="['fixed_table_box chg_time']"
      :headers="headers"
      :items="alarmSetupHistory"    
    >
    </DefaultTable>
   
</template>

<script>
  import dayjs from 'dayjs'
  import {mapState, mapActions} from 'vuex'
  import DefaultTable from '@/components/cctv/DefaultTable'
  export default {
    name: 'CctvHistory',
    components: {
      DefaultTable
    },
    computed: {
      ...mapState('cctv', ['alarmSetupHistory'])
    },
    data() {
      return {
         headers: [
          {id:'CHANGE_DT', name:'이력일시', cols_class:'w33p', cell_template: this.getTemplateChDt},
          {id:'USER_NM', name:'담당자', cols_class:'w34p'},
          {id:'CHANGE_CN', name:'변경분류', cols_class:'w33p'},
        ]
      }
    },
    methods: {
      getTemplateChDt(row, v) {
        // 00-00 (월) 00:00:00
        dayjs.locale("ko")
        
        return dayjs(v).format("MM월DD일(dd) HH:mm:ss")
      }
    }
  }
</script>


