<template>
  <DefaultTable 
    :tblClass="['fixed_table_box alarm_on']"
    :headers="headers"
    :items="alarmOnExceptSchedule"
    @cellClick="onCellClick"
  />
 
</template>
<script>
  import DefaultTable from '@/components/cctv/DefaultTable'
  import { mapActions, mapState} from 'vuex'
  import dayjs from 'dayjs'
  import commonCode from '@/utils/commonCode.js'
  export default {
    name: 'cctvAlarmOnExcept',
    components: {
      DefaultTable
    },
    computed: {
      ...mapState('cctv', ['alarmOnExceptSchedule'])
    },
    data(){
      return {
        headers: [
          {id:'BEGIN_DE', name:'시작', cols_class:'w24p', cell_template: this.getTemplateBeginHm},
          {id:'END_DE', name:'종료', cols_class:'w24p', cell_template: this.getTemplateEndHm},
          {id:'EXCP_TIME_NM', name:'시간대별', cols_class:'w14p', cell_template: this.getTemplateCycle},
          {id:'SCAN_SE_NM', name:'검지유형', cols_class:'w14p', cell_template: this.getTemplateScan},
          {id:'ACTION', name:'취소', cols_class:'w24p', type:'Button', label:"알람 ON 예외"},
        ],
        IA_EVENT_MLSFC_CODE: commonCode.IA_EVENT_MLSFC_CODE,
        ANALS_CND_CODE: commonCode.ANALS_CND_CODE
      }
    },
    methods: {
      ...mapActions('cctv', ['deleteAlarmOnExceptSchedule','getAlarmOnExceptSchedule']),
      getTemplateBeginHm(row, v) {
        dayjs.locale("ko")
        if (row.ALAR_CYCLE_CODE == 'N') {
          return dayjs(`${row.BEGIN_DE} ${row.BEGIN_HM}`, "YYYYMMDD HHmm").format("MM월DD일(dd) HH:mm:ss")
        } else {
          return dayjs(`19000101 ${row.BEGIN_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        }

      },
      getTemplateEndHm(row, v) {
        dayjs.locale("ko")
        if (row.ALAR_CYCLE_CODE == 'N') {
          return dayjs(`${row.END_DE} ${row.END_HM}`, "YYYYMMDD HHmm").format("MM월DD일(dd) HH:mm:ss")
        } else {
          return dayjs(`19000101 ${row.END_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        }

      },
      getTemplateScan(row,v) {
        try {
          return this.IA_EVENT_MLSFC_CODE
                    .filter(item => {
                      return v.split(",").includes(item.id)
                    })
                    .map(item => {
                      return item.name
                    })
                    .join(",")
        } catch(error) {
          return v
        }
      },
      getTemplateCycle(row, v) {
        try {
          return this.ANALS_CND_CODE
                      .filter(item => {
                        return v.split(",").includes(item.id)
                      })
                      .map(item => {
                        return item.name
                      })
                      .join(",")
        } catch(error) {
          return v
        }
      },
      async onCellClick(row, key) {
        if(key == 'ACTION') {
          const confirm = await this.$confirm('알람 ON 예외 취소할까요?')
          if(confirm) {
          
            this.deleteAlarmOnExceptSchedule(
              {
                cctvId: row.CCTV_ID, 
                creatDt: dayjs(row.CREAT_DT).format("YYYYMMDDHHmmssSSS")
              })
              .then(res =>{
                this.$info('알람 On 예외 처리 되었습니다.')
                this.getAlarmOnExceptSchedule(row.CCTV_ID)

              })
          }
        }
      }
    }

  }
</script>


