<template>
  <PopupLayout 
      :title="'CCTV 관리'"
      :popupClass="['popup_box','cctv_popup_box']"
      :isOpen="isOpen"
      @onClose="onPopupClose"
    >
   <div class="popup_bd">
      <div class="popup_left_box">

          <div class="popup_desc_box">
              <h4>{{cctvId}} ($해상도$) : {{scanDt}}</h4>
              <div class="input_box">
                  <button class="btn btn_md btn_secondary_outline">ROI 설정</button>
              </div>
          </div>
          <div class="cctv_box" style="text-align:center; background-Color:#000000">
            
            <img :src="image_data" :style="image_style"/> 
            <!--
            <video-player class="video-player-box"
                  ref="videoPlayer"
                  :options="playerOptions"
                  :playsinline="true"
                  customEventName="customstatechangedeventname"
  
                  @play="onPlayerPlay($event)"
                  @pause="onPlayerPause($event)"
                  @ended="onPlayerEnded($event)"
                  @waiting="onPlayerWaiting($event)"
                  @playing="onPlayerPlaying($event)"
                  @loadeddata="onPlayerLoadeddata($event)"
                  @timeupdate="onPlayerTimeupdate($event)"
                  @canplay="onPlayerCanplay($event)"
                  @canplaythrough="onPlayerCanplaythrough($event)"
  
                  @statechanged="playerStateChanged($event)"
                  @ready="playerReadied">
                </video-player>
            -->
          </div>

      </div>

      <div class="popup_right_box">

          <!-- tab_item 클릭시 active 클래스 토글되는 형태입니다 -->
          <!-- tab_menu -->
          <div class="tab_box type-a">
              <ul>
                
                  <li v-for="(key, index) in Object.keys(subTabs)"
                      :class="['tab_item', selectTab==key?'active':null]"
                      :key="index"
                      @click="onSelectTab(key)"
                      ><span>{{ subTabs[key] }}</span>
                  </li>
                  
              </ul>
          </div>
          <!-- //tab_menu -->

          <!-- 이벤트이력 -->
          <CctvEventHistory v-show="setDisplayInline('event')"></CctvEventHistory>
          <!-- 이벤트이력 -->

          <!-- CCTV 정보 -->
          <!-- 헤더고정이 아닌 경우의 테이블 -->
          <cctv-info v-show="setDisplayInline('cctv')"></cctv-info>
          <!-- // CCTV 정보 -->

          <!-- 제어정보 -->
          <cctv-preset v-show="setDisplayInline('control')"></cctv-preset>
          <!-- // 제어정보 -->

          <!-- 분석설정 -->
          <cctv-analyze :cctvId="cctvId" v-show="setDisplayInline('analysis')"></cctv-analyze>
          <!-- // 분석설정 -->

          <!-- 변경이력 -->
          <CctvHistory v-show="setDisplayInline('change')" />
          <!-- 변경이력 -->

          <!-- 알람Off 스케줄 -->
          <CctvAlarmOffSchedule :cctvId="cctvId"  v-show="setDisplayInline('alarmoff')"></CctvAlarmOffSchedule>
          <!-- 알람Off 스케줄 -->

          <!-- 알람On 예외처리 테이블 -->
          <CctvAlarmOnExcept  v-show="setDisplayInline('alarmon')" />
          <!-- 알람On 예외처리 테이블 -->

      </div>

  </div>
  </PopupLayout>
</template>
<script>
  // import 'video.js/dist/video-js.css'
  import {mapActions, mapState} from 'vuex'
  // import { videoPlayer } from 'vue-video-player'
  import PopupLayout from './PopupLayout.vue'
  import CctvInfo from './manager/CctvInfo.vue'
  import CctvPreset from './manager/CctvPreset.vue'
  import CctvHistory from './manager/CctvHistory.vue'
  import CctvAlarmOffSchedule from './manager/CctvAlarmOffSchedule.vue'
  import CctvAlarmOnExcept from './manager/CctvAlarmOnExcept'
  import CctvEventHistory from './manager/CctvEventHistory'
  import CctvAnalyze from './manager/CctvAnalyze'
  import io from 'socket.io-client'
  import dayjs from 'dayjs'

  export default {
    name: 'PopupCctv',
    props: {
      cctvId: '',
      src: '',
      isOpen: Boolean
    },
    components: {
      PopupLayout,
      CctvInfo,
      CctvPreset,
      CctvHistory,
      CctvAlarmOffSchedule,
      CctvAlarmOnExcept,
      CctvEventHistory,
      CctvAnalyze,
      // videoPlayer  
    },
    data() {
      return {
        scanDt: '',
        videoSrc: this.src,
        subTabs: {
          event: '이벤트이력',
          cctv: 'CCTV정보',
          control:'제어정보',
          analysis: '분석설정',
          change: '변경이력',
          alarmoff: '알람 OFF 스케쥴',
          alarmon: '알람ON 예외처리',
        },
        selectTab: 'event',
        image_data: '',
        image_style: {height:'50px'}, //
        sock: null,
        sockUri: `${window.location.protocol}//${window.location.host}`
      }
    },
    computed: {
      // player() {
      //   return this.$refs.videoPlayer.player
      // },
      // playerOptions() {
      //     // videojs options
      //   return {
            
      //       width: '996',
      //       height: '560',
      //       autoplay: true,
      //       muted: true,
      //       language: 'en',
      //       sources: [{
      //           type: "application/x-mpegURL",
      //           src: 'https://bitdash-a.akamaihd.net/content/MI201109210084_1/m3u8s/f08e80da-bf1d-4e3d-8899-f0f6155f6efa.m3u8' //this.src
      //       }],
      //       poster: "/static/images/author.jpg",
      //       controlBar:{
      //           remainingTimeDisplay: false,
      //           progressControl:false,
      //           playToggle:false,
      //           fullscreenToggle : false //    
      //       },
      //       sock: null,
      //       sockUri: `${window.location.protocol}//${window.location.host}`
      //   }
      // },
    },
    mounted() {
      // console.log('this is current player instance object', this.player)
    },
    methods:{
      ...mapActions('cctv',[
        'getCctvInfo',
        'getCctvPreset',
        'getAlarmSetupHistory',
        'getAlarmOnExceptSchedule',
        'getCctvOneAlarmOffSchedule',
        'getAlarmEventHistory',
        'getCctvAnalysis'
      ]),
      // onPlayerPlay(player) {
      //   // console.log('player play!', player)
      // },
      // onPlayerPause(player) {
      //   // console.log('player pause!', player)
      // },
      // onPlayerEnded(player){
      //   console.log('onPlayerEnded', player)
      // },
      // onPlayerWaiting(player){
      //   console.log('onPlayerWaiting', player)
      // },
      // onPlayerTimeupdate(player){
      //   // console.log('onPlayerTimeupdate', player)

      //   // 재생 시간 표현을 위한 데이터 추출
      //   // console.log('onPlayerTimeupdate', player.cache_.currentTime)
      //   // this.fnCapture()
      // },
      // onPlayerPlaying(player){
      //   // console.log('onPlayerPlaying', player)
      // },
      // onPlayerLoadeddata(player){
      //   console.log('onPlayerLoadeddata', player)
      // },
      // onPlayerCanplay(player){
      //   console.log('onPlayerCanplay', player)
      // },
      // onPlayerCanplaythrough(player){
      //   console.log('onPlayerCanplaythrough', player)
      // },
      // // or listen state event
      // playerStateChanged(playerCurrentState) {
      //   console.log('player current update state', playerCurrentState)
      // },
      // // player is ready
      // playerReadied(player) {
      //   console.log('the player is readied', player)
      //   // you can use it to do something...
      //   // player.[methods]
      // },
      onPopupClose() {
        if(this.sock) this.sock.close()
        this.$emit("onClose", false)
      },
      onSelectTab(key) {
        if(key) {
          this.selectTab = key
        } 
        switch(key){
          // cctv정보
          case "cctv":
            this.getCctvInfo(this.cctvId)
            break
          case "control":
            this.getCctvPreset(this.cctvId).then(res => {
                  $(".scroll_box2").mCustomScrollbar({
                    theme : "minimal-dark"
                  });
            })
            break      
          case "change":
            this.getAlarmSetupHistory(this.cctvId)
            break     
          case "alarmoff":
            this.getCctvOneAlarmOffSchedule(this.cctvId)       
            break
          case "alarmon":
            this.getAlarmOnExceptSchedule(this.cctvId)
            break;
          case "event":
            this.getAlarmEventHistory(this.cctvId).then(res => {
                  $(".scroll_box").mCustomScrollbar({
                    theme : "minimal-dark"
                  });
            })
            break;
          case "analysis":
            this.getCctvAnalysis(this.cctvId)
            break;            
   
        }

      },
      // display 텝클릭시 선택된 화면 노출
      setDisplayInline(key) {
        return this.selectTab == key? true : false 
      }
    },
    watch: {
      isOpen(v) {
        if(v) {
            this.sock = io(`http://localhost:3000/`) 
            // this.sock = io(`${this.sockUri}`) 
            this.image_data = '/static/EXcode/static/images/loading2.gif'
            this.image_style={height:'80px', marginTop:'230px'}      

            this.onSelectTab('event')
            this.getCctvInfo(this.cctvId).then(res => {
              this.scanDt = res.SCAN_DT ? dayjs(res.SCAN_DT).format("YYYY-MM-DD HH:mm:ss") : ''
              this.sock.on("stream", (messag) => {
                this.image_style={height:'560px', marginTop:'0px'}     
                // console.log(messag)
                this.image_data = `data:image/jpeg;base64,${messag[res.CCTV_ID]}`
              })
              // this.sock.emit("rtsp", {cctvId: res.CCTV_ID, src: 'rtsp://wowzaec2demo.streamlock.net/vod/mp4:BigBuckBunny_115k.mp4'})
              this.sock.emit("rtsp", {cctvId: res.CCTV_ID, src: `${res.RTSP_ADRES}`})

            })
        } else {
            if(this.sock) this.sock.close()
        }
      },
      cctvId(v) {
        if (v) this.onSelectTab(this.selectTab)
        
      }
    },

  }
</script>


