<template>
  <div :class="[...tblClass]">
    <!-- 헤더 고정 테이블인 경우 아래와 같이 사용해주세요. -->
      <slot name="table_tool"></slot>
      <div class="table_hd">
        <table>
            <colgroup>
                <col :class="head.cols_class||[]" v-for="(head, index) in headers" :key="index">
            </colgroup>
        <tbody>
        <slot name='table_head'></slot>
        <tr>
            <td  v-for="(head, index) in headers" :key="index">
                <span>{{ head.name }}</span>
                <div class="input_box sort_box" v-if="(head.isSort || 'N') =='Y'">
                    <button class="btn_sort" @click="fnSort(head.id, 'down')" v-if="sort[head.id] == 'down'"><img src="static/EXcode/static/images/ic_arrow_up_wh.png" alt=""></button>
                    <button class="btn_sort" @click="fnSort(head.id, 'up')" v-else><img src="static/EXcode/static/images/ic_arrow_down_wh.png" alt=""></button>
                </div>
            </td>
          </tr>
        </tbody>
        </table>
      </div>
      <div class="table_bd scroll_box">
        <table>
          <colgroup>
              <col :class="head.cols_class||[]" v-for="(head, index) in headers" :key="index">
          </colgroup>
          <tbody>
          <tr v-for="(row, row_idx) in items" :key="row_idx" @click="row_click(row, row_idx)">
              <td  v-for="(head, index) in headers" :key="index" >

                  <span v-if="typeof head.cell_template == 'function'" v-html="head.cell_template(row, row[head.id], cell_click)"  @click="cell_click(row, head.id)" ></span>
                  <span v-else-if="['Button'].includes(head.type)">
                    <components 
                      :is="head.type" 
                      :label="head.label" 
                      v-bind="row" 
                      @onClick="cell_click(row, head.id)"  />
                  </span>
                  <span v-else  @click="cell_click(row, head.id)">{{ row[head.id] }}</span>
              </td>
          </tr>
          </tbody>
        </table>
      </div>
      <slot name='bottom_tool'></slot>
  </div>
</template>

<script>
  import Button from '@/components/cctv/Button'
  export default {
    name: 'DefaultTable',
    props: {
      headers: Array,
      items: Array,
      tblClass: {
        type: [Array, String],
        default: ''
      },
      inSort: {
        type: [Object, String],
        detault: () => {
          return {}
        }
      }
    },
    data() {
      return {
        sort: {}
      }
    },
    components: {
      Button
    },
    computed: {
      // isHeaderMulty() {
      //   return Array.isArray(this.headers[0].name)
      // },
    },
    methods: {
      row_click(row, index) {
        this.$emit('rowClick',row, index)
      },
      cell_click(row, head_id, e) {
        this.$emit('cellClick',row, head_id)
      },
      fnSort(key, updown) {
        const isup = updown == 'up' ? 'down': 'up'
        this.sort = {
          [key]: isup
        }
        this.$emit('sort', key, isup)
      }
    }
  } 
</script>


