<template>
  <DefaultTable
    :tblClass="['fixed_table_box event_detail']"
    :headers="headers"
    :items="alarmEventHistory"
    @cellClick="onCellClick"

  >
      <template slot="table_head">
        <tr class="table_hd_category">
            <td colspan="6"><span>1차 검지</span></td>
        </tr>
    </template>

  </DefaultTable>

</template>
<script>
  import DefaultTable from '@/components/cctv/DefaultTable'
  import {mapState} from 'vuex'
  import dayjs from 'dayjs'
  export default {
    name: 'CctvEvnetHistory',
    components: {
      DefaultTable
    },
    data() {
      return {
        headers: [
          {id:'SCAN_GRAD', name:'상황', cols_class:'w10p', cell_template: this.getTemplateGrad},
          {id:'SCAN_DT', name:'검지일시', cols_class:'w21p', cell_template: this.getTemplateScanDt},
          {id:'SCD_SCAN_TY_NM', name:'검지유형', cols_class:'w14p'},
          {id:'SCAN_RESULT_DTL_CODE', name:'검지구분', cols_class:'w14p', cell_template: this.getTemplateScReDtCode},
          {id:'IS_SCAN_TY_CODE', name:'검지비교', cols_class:'w14p'},
          {id:'SCAN_RESULT_MEMO_CN', name:'메모', cols_class:'w27p'},
          
        ]
      }
    },
    computed: {
      ...mapState('cctv', ['alarmEventHistory'])
    },
    methods: {
      getTemplateGrad(row, v) {
        return `${v}급`
      },
      getTemplateScanDt(row, v) {
        return dayjs(v).format("MM-DD HH:mm:ss")
      },
      getTemplateScReDtCode(row, v) {
        return row.SCAN_RESULT_CODE == 'ETC' ? '기타': row.SCAN_RESULT_DTL_CODE
      },
      onCellClick(row, key) {
        if(key == 'IS_SCAN_TY_CODE'){
           row = { ...row, SCAN_DT: dayjs(row.SCAN_DT).format("YYYY-MM-DD HH:mm:ss") }
              
           if (typeof this.$parent.$parent.$parent.eventPopOpen == 'function') {
              // 이벤트 메뉴에서 
              
              this.$parent.$parent.$parent.eventPopOpen(row)
           } else {
              this.$parent.$parent.$parent.eventOpenPop(row)
           }
           this.$parent.$parent.$parent.onManagerPopupClose()

        }
      }
      
    }
  }
</script>


