<template>
    <div class="basic_table_box cctvinfo">
          
        <div class="table_wrap">
            <table>
                <colgroup>
                    <col class="w25p">
                    <col class="w25p">
                    <col class="w25p">
                    <col class="w25p">
                </colgroup>
                <tbody>

                    <tr>
                        <th><span>CCTV ID</span></th>
                        <td colspan="3"><span>{{cctvInfo.CCTV_ID}}</span></td>
                    </tr>

                    <tr>
                        <th><span>관리청</span></th>
                        <td><span>{{ cctvInfo.INTD_CODE_NM }}</span></td>
                        <th><span>제조사명</span></th>
                        <td><span>{{ cctvInfo.MAKR_NM }}</span></td>
                    </tr>
                    <tr>
                        <th><span>설치위치</span></th>
                        <td><span>{{ cctvInfo.INSTL_LC }}</span></td>
                        <th><span>모델명</span></th>
                        <td><span>{{ cctvInfo.MODEL_NM }}</span></td>
                    </tr>
                    <tr>
                        <th><span>지도좌표</span></th>
                        <td><span>위도 {{cctvInfo.MAP_XCORD}}/경도 {{cctvInfo.MAP_YCORD}}</span></td>
                        <th><span>검지방향</span></th>
                        <td><span>{{ cctvInfo.INSTL_DRC_CODE }}</span></td>
                    </tr>
                    <tr>
                        <th><span>설치일시</span></th>
                        <td><span>{{ getInstlDt(cctvInfo.INSTL_DT) }}</span></td>
                        <th><span>통신상태</span></th>
                        <td><span>{{ cctvInfo.COMM_STTUS_CODE }}</span></td>
                    </tr>
                    <tr>
                        <th><span>송신지</span></th>
                        <td><span>송신지내용</span></td>
                        <th><span>순번</span></th>
                        <td><span>순번내용</span></td>
                    </tr>
                    <tr>
                        <th><span>목적지</span></th>
                        <td><span>목적지내용</span></td>
                        <th><span>제어기번호</span></th>
                        <td><span>제어기번호내용</span></td>
                    </tr>
                    <tr>
                        <th><span>운영코드</span></th>
                        <td><span>{{ cctvInfo.COMM_TY_CODE }}</span></td>
                        <th><span>통신IP</span></th>
                        <td><span>{{ cctvInfo.CNTLR_RIP_ADRES }}</span></td>
                    </tr>
                    <tr>
                        <th><span>응답여부</span></th>
                        <td><span>##응답여부##</span></td>
                        <th><span>통신 포트</span></th>
                        <td><span>{{ cctvInfo.CNTLR_PORT_NO }}</span></td>
                    </tr>

                </tbody>
            </table>
        </div>

    </div>
</template>
<script>
  import {mapActions, mapState} from 'vuex'
  import dayjs from 'dayjs'
  export default {
    name: 'cctvInfo',
    components: {
      dayjs
    },
    props: {
        CCTV_ID: ''
    },
    computed: {
        ...mapState('cctv', ['cctvInfo'])
    },
    methods: {
        getInstlDt(dt) {
            return dayjs(dt).format("MM-DD HH:mm:ss")
        }
    }
  }
</script>


