<template>
  <div class='input_box'>
      <button class='dis_ib btn btn_md btn_secondary' @click="onClickEvent()">{{label}}</button>
  </div>
</template>
<script>
  export default {
    name: 'Button',
    props: {
      label: {
        type: String,
        default: '관리'
      }
    },
    methods: {
      onClickEvent() {
        this.$emit("onClick",{})
      }
    }
  }

</script>


