<template>
  <div class="basic_table_box analyze_info" >

      <div class="table_wrap">
          <table>
              <colgroup>
                  <col class="w20p">
                  <col class="w15p">
                  <col class="w25p">
                  <col class="w15p">
                  <col class="w25p">
              </colgroup>
              <tbody>
                <template v-for="(obj, index) in ANALS_TTL">
                <tr>
                      <th rowspan="2"><span v-html="obj.title"></span></th>
                        <template v-for="(rw, idx) in getAnalsCndCode(obj.key, 0)">
                        <th><span>{{ rw.name }}</span></th>
                        <td>
                            <div class="input_group">
                                <label class="input_box radio_box">
                                    <input type="radio" v-model="cctvAnalysis[rw.id]" value="Y">
                                    <span>가능</span>
                                </label>
                                <label class="input_box radio_box">
                                    <input type="radio" v-model="cctvAnalysis[rw.id]" value="N">
                                    <span>불가능</span>
                                </label>
                            </div>
                        </td>
                      </template>
                  
                  </tr>
                  <tr>
                    <template v-for="(rw, idx) in getAnalsCndCode(obj.key, 1)">
                      <th><span>{{ rw.name }}</span></th>
                      <td>
                          <div class="input_group">
                              <label class="input_box radio_box">
                                  <input type="radio"  v-model="cctvAnalysis[rw.id]" value="Y">
                                  <span>가능</span>
                              </label>
                              <label class="input_box radio_box">
                                  <input type="radio"  v-model="cctvAnalysis[rw.id]" value="N">
                                  <span>불가능</span>
                              </label>
                          </div>
                      </td>
                    </template>
                  
                  </tr>
              </template>
            
              </tbody>
          </table>

          <div class="input_box txt_c mt10">
              <button class="btn btn_md btn_primary dis_ib" @click="onAnalyzeSetup">설정저장</button>
          </div>
      </div>

  </div>

</template>
<script>
  import CMMCD from '@/utils/commonCode'
  import {mapState, mapActions} from 'vuex'
  export default {
    name: 'CctvAnalyze',
    props: {
      cctvId: ''
    },
    data(){
      return {
        ANALS_TTL: [
          { key: 'T', title: '시간대별<br/>분석여부'},
          { key: 'W', title: '날씨별<br/>분석여부'},
          { key: 'S', title: '계절별<br/>분석여부'},
          { key: 'E', title: '환경별<br/>분석여부'},
        ],
        ANALS_CND_CODE: CMMCD.ANALS_CND_CODE,
        analsSett: {}
      }
    },
    computed: {
      ...mapState('cctv', ['resCctvAnalysis','cctvAnalysis'])
    },
    mounted() {
      console.log('CctvAnalyze', CMMCD)
    },
    methods: {
      ...mapActions('cctv', ['putCctvAnalysis', 'getCctvAnalysis']),
      getAnalsCndCode(key, idx) {
          const Reg = new RegExp(`^${key}.+`)
          if(idx == 0) {
            return this.ANALS_CND_CODE.filter((item, index) => { return Reg.exec(item.id) && index % 2 == 0 })
          } else {
            return this.ANALS_CND_CODE.filter((item, index) => { return Reg.exec(item.id) && index % 2 != 0 })
          }
      },
      onAnalyzeSetup () {
        // const ch = Object.keys(this.analsSett).map(key => {
        //   return this.analsSett[key] || '' 
        // })
        const params = {
          cctvId : this.cctvId,
          analysis: this.cctvAnalysis
        }
        this.putCctvAnalysis(params).then(res => {
          this.$info('분석 설정이 저장되었습니다.')          

          this.getCctvAnalysis(this.cctvId)
        })

      }
    },
    watch: {
    }
  }
</script>


