<template>
<div>
    <DefaultTable
      :tblClass="['fixed_table_box alarm_off']"
      :headers="headers"
      :items="cctvOneAlarmOffSchedule"
      @cellClick="onCellClick"
    >
     <template slot="bottom_tool">
        <div class="input_box txt_c"> 
          <button class="btn btn_md btn_primary dis_ib" @click="onOffSetup">알람OFF 설정</button>
        </div>
            
    </template>
    
    </DefaultTable>
    
  </div>
</template>
<script>
  import {mapState, mapActions} from 'vuex'
  import DefaultTable from '@/components/cctv/DefaultTable'
  import dayjs from 'dayjs'
  import commonCode from '@/utils/commonCode.js'

  export default {
    name: 'AlarmOffSchedule',
    props: {
      cctvId :''
    },
    components: {
      DefaultTable,
      dayjs
    },
    data() {
      return {
         headers: [
          {id:'BEGIN_DE', name:'시작', cols_class:'w24p', cell_template: this.getTemplateBegnDt},
          {id:'END_DE', name:'종료', cols_class:'w24p', cell_template: this.getTemplateEndDt},
          {id:'ALAR_CYCLE_CODE', name:'반복', cols_class:'w14p', cell_template: this.getTemplateCycleCode},
          {id:'SCAN_SE_NM', name:'검지유형', cols_class:'w14p', cell_template: this.getTemplateScan},
          {id:'ACTION', name:'취소', cols_class:'w24p', type:'Button', label:"알람OFF취소"},
        ],
        ALAR_CYCLE_CODE: commonCode.ALAR_CYCLE_CODE,
        IA_EVENT_MLSFC_CODE: commonCode.IA_EVENT_MLSFC_CODE
      }
    },
    computed: {
      ...mapState('cctv', ['cctvOneAlarmOffSchedule'])
    },
    methods: {   
      ...mapActions('cctv',['deleteAlarmOffSchedule','getCctvOneAlarmOffSchedule']),
      getTemplateBegnDt(row, v) {
        dayjs.locale("ko")
        if (row.ALAR_CYCLE_CODE == 'N') {
          return dayjs(`${row.BEGIN_DE} ${row.BEGIN_HM}`, "YYYYMMDD HHmm").format("MM월DD일(dd) HH:mm:ss")
        } else {
          
          return dayjs(`19000101 ${row.BEGIN_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        }
      },
      getTemplateEndDt(row, v) {
        dayjs.locale("ko")
        if (row.ALAR_CYCLE_CODE == 'N') {
          return dayjs(`${row.END_DE} ${row.END_HM}`, "YYYYMMDD HHmm").format("MM월DD일(dd) HH:mm:ss")
        } else {
          return dayjs(`19000101 ${row.END_HM}`, "YYYYMMDD HHmm").format("HH:mm")
        }
      },
      getTemplateCycleCode(row, v) {
        try {
          return this.ALAR_CYCLE_CODE[v]
        } catch(error) {
          return v
        }
      },
      getTemplateScan(row,v) {
        try {
          return this.IA_EVENT_MLSFC_CODE
                    .filter(item => {
                      return v.split(",").includes(item.id)
                    })
                    .map(item => {
                      return item.name
                    })
                    .join(",")
        } catch(error) {
          return v
        }
      },
      onCellClick(row, key) {
        if(key == 'ACTION') {
          this.deleteAlarmOffSchedule(
            {
              cctvId: row.CCTV_ID, 
              creatDt: dayjs(row.CREAT_DT).format("YYYYMMDDHHmmssSSS")
            })
            .then(res =>{
              this.$info('알람 off 제외 되었습니다.')
              this.getCctvOneAlarmOffSchedule(row.CCTV_ID)       
            })

        }
      },
      onOffSetup() {
        this.$parent.$parent.onPopupClose()
        this.$parent.$parent.$parent.onAllAlarmPopupOpen(this.cctvId)

        
      }
    },
    watch: {
      cctvOneAlarmOffSchedule(v, v2) {
        console.log(v, v2)
      }
    }
  }
</script>


