<template>

    <div class="basic_table_box control_info">

        <div class="table_wrap scroll_box2">
            <table>
                <colgroup>
                    <col class="w25p">
                    <col class="w25p">
                    <col class="w25p">
                    <col class="w25p">
                </colgroup>
                <tbody>

                    <tr>
                        <th><span>검지영역</span></th>
                        <td><span>정상</span></td>
                        <th><span>시정미확보</span></th>
                        <td><span>검지영역축소</span></td>
                    </tr>

                </tbody>
            </table>
            <template  v-for="(row, index) in cctvPreset" >
                <table class="mt10" :key="index">
                    <colgroup>
                        <col class="w25p">
                        <col class="w25p">
                        <col class="w25p">
                        <col class="w25p">
                    </colgroup>
                    <tbody>

                        <tr>
                            <th><span>Preset</span></th>
                            <td colspan="3"><span>{{row.PRESET_NM}}/{{row.PRESET_NO}}</span></td>
                        </tr>

                        <tr>
                            <th rowspan="4" style="border-right: 1px solid #e6e7e8"><span>관리청</span></th>
                            <th><span>Pan위치</span></th>
                            <td colspan="2"><span>{{ row.PAN_VALUE }}</span></td>
                        </tr>
                        <tr>
                            <th><span>Tilt위치</span></th>
                            <td colspan="2"><span>{{ row.TILT_VALUE }}</span></td>
                        </tr>
                        <tr>
                            <th><span>Zoom위치</span></th>
                            <td colspan="2"><span>{{ row.ZOOM_VALUE }}</span></td>
                        </tr>
                        <tr>
                            <th><span>포커스 위치</span></th>
                            <td colspan="2"><span>{{ row.FOCUS_VALUE }}</span></td>
                        </tr>

                    </tbody>
                </table>
            </template>
       
        </div>

        <div class="control_box">

            <div class="input_group">

                <div class="input_box control_btn_box vertical_control">
                    <button class="btn control_btn">위로 이동</button>
                    <button class="btn control_btn">아래로 이동</button>
                </div>
                <div class="input_box control_btn_box horizon_control">
                    <button class="btn control_btn">왼쪽으로 이동</button>
                    <button class="btn control_btn">오른쪽으로 이동</button>
                </div>
                <div class="input_box control_btn_box zoom_control">
                    <button class="btn control_btn">확대하기</button>
                    <button class="btn control_btn">축소하기</button>
                </div>
                <div class="input_box">
                    <button class="btn btn_lg btn_primary btn_lg_txt">화면저장하기</button>
                </div>

            </div>

        </div>

    </div>
</template>
<script>
  import {mapState} from 'vuex'
  export default {
    name:'CctvPreset',
    computed: {
        ...mapState('cctv', ['cctvPreset'])
    }
  } 
</script>


